*{
  box-sizing: border-box;
 padding: 0;
 margin: 0;
}

body{
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
}

a{
  color: #333;
  text-decoration: none;
}

.btn{
  background-color: black;
  color: white;
  display: inline-block;
  cursor: pointer;
  padding: 7px 10px;
  border: none;
  font-size: larger;
}